import * as React from 'react';
import {useEffect, useState} from 'react';
import {FixMessage, FixMessageTagKind} from '../domain';
import {Chip, Grid} from '@mui/material';
import TableContainer from '@mui/material/TableContainer';
import Paper from '@mui/material/Paper';
import Table from '@mui/material/Table';
import TableHead from '@mui/material/TableHead';
import TableRow from '@mui/material/TableRow';
import TableCell from '@mui/material/TableCell';
import TableBody from '@mui/material/TableBody';
import {styled} from '@mui/material/styles';


const Item = styled(Paper)(({ theme }) => ({
    padding: theme.spacing(1),
}));

const TAG_DESCRIPTION_MAPPING = new Map<string,  'default' | 'primary' | 'secondary' | 'error' | 'info' | 'success' | 'warning'>();
TAG_DESCRIPTION_MAPPING.set(FixMessageTagKind.CUSTOM, 'primary');
TAG_DESCRIPTION_MAPPING.set(FixMessageTagKind.HEADER, 'success');
TAG_DESCRIPTION_MAPPING.set(FixMessageTagKind.SYSTEM, 'secondary');

export const FixViewer = ({fixMessages} : {fixMessages: FixMessage[]}) => {
    const [selectedMessage, setSelectedMessage] = useState<FixMessage | null>(null);
    const [selectedMessageIndex, setSelectedMessageIndex] = useState<number | null>(null);

    useEffect(() => {
        if (!fixMessages) {
            setSelectedMessage(null);
            setSelectedMessageIndex(null);
        }
    }, [fixMessages]);

    return (
        <>
            <Grid container>
                <Grid xs={7}>
                    <Item>
                        <TableContainer component={Paper}>
                            <Table>
                                <TableHead>
                                    <TableRow>
                                        <TableCell>time</TableCell>
                                        <TableCell>sender</TableCell>
                                        <TableCell>target</TableCell>
                                        <TableCell>message type</TableCell>
                                        <TableCell>client order id</TableCell>
                                    </TableRow>
                                </TableHead>
                                <TableBody>
                                    {
                                        fixMessages.map((row, i) => {
                                            return (
                                                <TableRow
                                                    key={i}
                                                    onClick={() => {
                                                        setSelectedMessage(row);
                                                        setSelectedMessageIndex(i);
                                                    }}
                                                    style={{
                                                        cursor: 'pointer',
                                                        background: (selectedMessageIndex != null && i == selectedMessageIndex) ? 'rgb(237, 149, 38)' : 'transparent',
                                                    }}
                                                >
                                                    <TableCell>{row.time}</TableCell>
                                                    <TableCell>{row.sender}</TableCell>
                                                    <TableCell>{row.target}</TableCell>
                                                    <TableCell>{row.messageTypeDescription}</TableCell>
                                                    <TableCell>{row.clorId}</TableCell>
                                                </TableRow>
                                            );
                                        })
                                    }
                                </TableBody>
                            </Table>
                        </TableContainer>
                    </Item>
                </Grid>
                <Grid xs={5}>
                    <Item>
                        {selectedMessage && (
                            <TableContainer component={Paper}>
                                <Table>
                                    <TableHead>
                                        <TableRow>
                                            <TableCell>Tag</TableCell>
                                            <TableCell>Tag Description</TableCell>
                                            <TableCell>Value</TableCell>
                                            <TableCell>Value Description</TableCell>
                                        </TableRow>
                                    </TableHead>
                                    <TableBody>
                                        {
                                            selectedMessage.tags.map(tag => {
                                                return (
                                                    <TableRow key={tag.tag}>
                                                        <TableCell>{tag.tag}</TableCell>
                                                        <TableCell>
                                                            <Chip color={TAG_DESCRIPTION_MAPPING.get(tag.kind)} label={tag.tagDescription} size="small" />
                                                        </TableCell>
                                                        <TableCell>{tag.value}</TableCell>
                                                        <TableCell>{tag.valueDescription}</TableCell>
                                                    </TableRow>
                                                );
                                            })
                                        }
                                    </TableBody>
                                </Table>
                            </TableContainer>
                        )}
                    </Item>
                </Grid>
            </Grid>
        </>
    );
}
