import {FixMessage, FixMessageTag, FixTag} from '../domain';
import {resolveEnumValue, resolveTagId, resolveTagKind, resolveTagName} from "./thesaurus";

export const parseFixMessages = (data: string) : FixMessage[] => {
    const lines: string[] = data.split('\n');
    const messages: FixMessage[] = [];
    for (const line of lines) {
        const message = parseFixMessage(line);
        if (message != null) {
            messages.push(message);
        }
    }

    return messages;
}

export const detectFixMessageSeparator = (line: string) : string | null => {
    const separatorPattern: RegExp = /8=FIXT?\.\d\.\d([^\d]+)/i;
    const matches = separatorPattern.exec(line);
    if (matches && matches.length === 2) {
        return matches[1];
    }

    return null;
}

export const parseFixTagsFromLine = (line: string, separator: string) : FixTag[] => {
    return line
        .split(separator)
        .filter(m => m && m.length > 0)
        .map(part => {
            const delimiterPos = part.indexOf('=');
            if (delimiterPos == -1) {
                return {
                    key: Number.parseInt(part),
                    value: null,
                };
            }

            const tagId = part.substring(0, delimiterPos);
            return {
                key: Number.parseInt(tagId),
                value: part.substring(delimiterPos + 1),
            }
        })
        .filter(part => part != null);
}

export const populateFixTag = (tag: FixTag) : FixMessageTag => {
    return {
        tag: tag.key,
        tagDescription: resolveTagName(tag.key),
        value: tag.value,
        valueDescription: resolveEnumValue(tag.key, tag.value),
        kind: resolveTagKind(tag.key),
    };
}

export interface ShortTagsIndex {
    [Key: number]: string;
}

export const createShortIndex = (tags: FixTag[], scan: number[]): ShortTagsIndex => {
    const scanIndex = new Set(scan);
    const index: ShortTagsIndex = {};
    for (const tag of tags) {
        if (scanIndex.has(tag.key)) {
            index[tag.key] = tag.value != null ? tag.value : '';
        }
    }
    for (const tagId of scan) {
        if (index[tagId] === undefined) {
            index[tagId] = '';
        }
    }

    return index;
}

export const populateFixMessage = (tags: FixTag[]) : FixMessage => {
    const mainTags = createShortIndex(tags, [
        resolveTagId('ClOrdID'),
        resolveTagId('SenderCompID'),
        resolveTagId('TargetCompID'),
        resolveTagId('SendingTime'),
        resolveTagId('MsgType'),
    ]);

    return {
        time: mainTags[resolveTagId('SendingTime')],
        sender: mainTags[resolveTagId('SenderCompID')],
        target: mainTags[resolveTagId('TargetCompID')],
        messageType: mainTags[resolveTagId('MsgType')],
        messageTypeDescription: resolveEnumValue(resolveTagId('MsgType'), mainTags[resolveTagId('MsgType')]),
        message: tags.map(kv => `${kv.key}=${kv.value}`).join("|"),
        clorId: mainTags[resolveTagId('ClOrdID')],

        tags: tags.map(t => populateFixTag(t)),
    }
}

const SHORTEST_MESSAGE_LENGTH = 12;
export const parseFixMessage = (line: string) : FixMessage | null => {
    const firstIndex = line.indexOf("8=FIX");
    if (firstIndex === -1 || line.length - firstIndex < SHORTEST_MESSAGE_LENGTH) {
        return null;
    }

    const separator = detectFixMessageSeparator(line.substring(firstIndex));
    if (separator == null) {
        return null;
    }

    const messageParts: FixTag[] = parseFixTagsFromLine(line.substring(firstIndex), separator);
    return populateFixMessage(messageParts);
}
