import * as React from 'react';
import {useState} from 'react';
import AppBar from '@mui/material/AppBar';
import Box from '@mui/material/Box';
import Toolbar from '@mui/material/Toolbar';
import Typography from '@mui/material/Typography';
import IconButton from '@mui/material/IconButton';
import LinkedInIcon from '@mui/icons-material/LinkedIn';
import AccountBalanceIcon from '@mui/icons-material/AccountBalance';
import {Container, Link} from '@mui/material';
import {FixInput} from './components/FixInput';
import {parseFixMessages} from './utils/parser';
import {FixMessage} from './domain';
import {FixViewer} from './components/FixViewer';


const MainApp = () => {
    const [fixMessages, setFixMessages] = useState<FixMessage[]>([]);

  return (
      <>
          <Box sx={{ flexGrow: 1 }}>
              <AppBar position="static">
                  <Toolbar>
                      <IconButton
                          size="large"
                          edge="start"
                          color="inherit"
                          aria-label="menu"
                          sx={{ mr: 2 }}
                      >
                          <AccountBalanceIcon></AccountBalanceIcon>
                      </IconButton>
                      <Typography variant="h6" component="div" sx={{ flexGrow: 1 }}>
                          FIX Parser
                      </Typography>
                      <IconButton color="inherit">
                          <Link color="inherit" target="_blank" href="https://www.linkedin.com/in/mrk-andreev/">
                              <LinkedInIcon />
                          </Link>
                      </IconButton>
                  </Toolbar>
              </AppBar>
          </Box>
          <Container>
              <Box sx={{ my: 2 }}>
                  <Typography component="div" sx={{ mb: 12 }}>
                      <FixInput onSubmit={(nextValue: string) => {setFixMessages(parseFixMessages(nextValue))}} />
                  </Typography>
              </Box>
          </Container>
          <Container>
              <FixViewer fixMessages={fixMessages} />
          </Container>
      </>
  );
}

export default MainApp;
