import * as React from 'react';
import {useState} from 'react';
import {Button, ButtonGroup, TextField} from '@mui/material';
import Box from '@mui/material/Box';
import {nextSampleFixMessage} from "../utils/thesaurus";
import './FixInput.css';

export const FixInput = ({onSubmit} : {onSubmit: (value:string) => void}) => {
    const [fixValue, setFixValue] = useState('');

    return (
        <>
            <TextField
                label="Paste FIX text below. Your FIX data is not sent to any server."
                multiline
                fullWidth
                rows={4}
                size="small"
                value={fixValue}
                onChange={(next) => setFixValue(next.target.value)}
                autoFocus={true}
                className='fix-message-input'
            />
            <Box
                display="flex"
                justifyContent="flex-end"
                sx={{ my: 1 }}
            >
                <ButtonGroup variant="contained">
                    <Button
                        color="success"
                        onClick={() => onSubmit(fixValue)}
                    >Parse</Button>
                    <Button
                        variant="outlined"
                        onClick={() => setFixValue(nextSampleFixMessage())}
                    >Random sample</Button>
                </ButtonGroup>
            </Box>
        </>
    );
};
