import {FixMessageTagKind} from '../domain';
import FIX44_TAGS_ENUMS from '../thesaurus/FIX44_TAGS_ENUMS.json';
import FIX44_TAGS from '../thesaurus/FIX44_TAGS.json';
import FIX44_TAGS_INVERSE from '../thesaurus/FIX44_TAGS_INVERSE.json';

export const resolveEnumValue = (tagId: number, value: string | null): string => {
    // @ts-expect-error: Should decode json
    if (Object.prototype.hasOwnProperty.call(FIX44_TAGS_ENUMS, tagId.toString()) && Object.prototype.hasOwnProperty.call(FIX44_TAGS_ENUMS[tagId.toString()], value)) {
        // @ts-expect-error: Should decode json
        return FIX44_TAGS_ENUMS[tagId.toString()][value];
    }

    return '';
}

export const resolveTagName = (tagId: number): string => {
    // @ts-expect-error: Should decode json
    return FIX44_TAGS[tagId.toString()];
}

export const resolveTagId = (tagName: string): number => {
    // @ts-expect-error: Should decode json
    return FIX44_TAGS_INVERSE[tagName.toString()];
}

const SYSTEM_TAGS = new Set([35, 150]);
const HEADER_TAGS = new Set([8,9,35,49,56,34,52,10]);

export const resolveTagKind = (tagId: number): FixMessageTagKind => {
    if (SYSTEM_TAGS.has(tagId)) {
        return FixMessageTagKind.SYSTEM;
    }
    if (HEADER_TAGS.has(tagId)) {
        return FixMessageTagKind.HEADER;
    }

    return FixMessageTagKind.CUSTOM;
}

